  padding-left: 14px;
  padding-left: 4px \9;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  border-radius: 15px;
  -webkit-appearance: textfield;
  -webkit-box-sizing: content-box; }

label {
  display: inline-block;
  min-height: 20px;
  margin-right: 10px;
  margin-bottom: 10px;
  vertical-align: middle;
  font-weight: normal; }

input[type="radio"],
input[type="checkbox"] {
  line-height: normal;
  vertical-align: middle; }

label input[type="radio"],
label input[type="checkbox"] {
  float: left;
  margin: 4px 0 0;
  margin-top: 1px \9;
  *margin-top: 0;
  margin-right: 3px;
  margin-left: 0; }

label,
select,
button,
input[type="button"],
input[type="reset"],
input[type="submit"],
